@import '~assets/styles/h3Imports';

.notification-counter {
  background-color: var(--notification-counter-background, #e10900);
  border-radius: 50%;
  color: var(--notification-counter-color, #ffffff);
  font-size: rem(14);
  height: rem(24);
  line-height: 0;
  position: relative;
  width: rem(24);
}

.notification-number {
  bottom: 50%;
  position: absolute;
  text-align: center;
  width: 100%;
}
